import Vue from "vue";
import Router from "vue-router";
import Diary from "@/views/Diary.vue";
import Subject from "@/views/Subject.vue";
import Journal from "@/views/Journal.vue";
import Grade from "@/views/Grade.vue";
import Settings from "@/views/Settings.vue";

Vue.use(Router);
export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "Home",
      meta: {
        layout: "info"
      },
      component: () => import("@/views/Home.vue")
    },
    {
      path: "/about",
      name: "About",
      meta: {
        layout: "info"
      },
      component: () => import("@/views/About.vue")
    },
    {
      path: "/login",
      name: "Login",
      component: () => import("@/views/Login.vue")
    },
    {
      path: "/login/:lid",
      name: "Connect User",
      component: () => import("@/views/Login.vue")
    },
    {
      path: "/diary",
      name: "Дневник",
      meta: {
        layout: "main"
      },
      component: Diary
    },
    {
      path: "/subject",
      name: "Предмет",
      meta: {
        layout: "main"
      },
      component: Subject
    },
    {
      path: "/journal",
      name: "Журнал",
      meta: {
        layout: "main"
      },
      component: Journal
    },
    {
      path: "/grade",
      name: "Итоги",
      meta: {
        layout: "main"
      },
      component: Grade
    },
    {
      path: "/settings",
      name: "Настройки",
      meta: {
        layout: "main"
      },
      component: Settings
    }
  ]
});
