<template>
  <md-card
    class="md-layout-item md-xsmall-size-95 md-small-size-95 md-medium-size-45 md-large-size-45 md-xlarge-size-30"
  >
    <md-card-header>
      <span class="md-title">{{ day.date | date }}</span>
    </md-card-header>

    <md-card-content>
      <Lesson
        :class="{ stripes }"
        v-for="lesson in day.lessons"
        :key="lesson.id"
        :name="lesson.name"
        :room="lesson.room"
        :number="lesson.number"
        :endTime="lesson.endTime"
        :homework="lesson.homework"
        :startTime="lesson.startTime"
        :assignments="lesson.assignments"
      />
    </md-card-content>
  </md-card>
</template>

<script>
import { mapState } from "vuex";
import Lesson from "./DiaryLesson";

export default {
  props: ["day"],
  computed: mapState({
    stripes: state => state.settings.storage.diaryStripes
  }),
  methods: {
    marks(assignments) {
      return assignments?.filter(a => a.mark) || [];
    },
    homework(assignments) {
      return assignments?.find(a => a.type == 3)?.name || "";
    }
  },
  filters: {
    date(val) {
      if (typeof val == "string") val = new Date(Date.parse(val));
      const week = ["Вс", "Пн", "Вт", "Ср", "Чт", "Пт", "Сб"];
      const month = [
        "янв",
        "фев",
        "мар",
        "апр",
        "май",
        "июн",
        "июл",
        "авг",
        "сен",
        "окт",
        "ноя",
        "дек"
      ];
      return (
        `${week[val.getDay()]}, ` +
        `${val.getDate()} ` +
        `${month[val.getMonth()]}. ` +
        `${val.getFullYear()} г.`
      );
    }
  },
  components: {
    Lesson
  }
};
</script>
