<template>
  <div class="content" :class="name">
    <Loader v-if="loading" />
    <div id="header">
      <span class="md-headline">{{ loading ? "Загрузка..." : name }}</span>
      <md-menu
        id="user-info"
        md-size="auto"
        md-align-trigger
        md-close-on-select
      >
        <md-button md-menu-trigger>
          <UserInfo :id="uid" />
        </md-button>

        <md-menu-content>
          <md-menu-item :to="{ name: 'Login', params: { addUser: true } }">
            <md-icon>add</md-icon>
            <span>Добавить пользователя</span>
          </md-menu-item>
          <md-menu-item
            v-for="id in connectedUsers"
            v-show="id != uid"
            @click="uid = id"
            class="flex-end"
            :key="id"
          >
            <UserInfo :id="id" />
          </md-menu-item>
        </md-menu-content>
      </md-menu>
    </div>
    <router-view v-if="!loading" />
    <Sidebar :work="!loading" />
    <md-snackbar :md-active="!!alert" @update:mdActive="alert = false">
      <span>{{ alert }}</span>
    </md-snackbar>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Loader from "@/components/Loader.vue";
import Sidebar from "@/components/Sidebar.vue";
import UserInfo from "@/components/UserInfo.vue";

export default {
  data: () => ({
    alert: false,
    loading: true
  }),
  methods: {
    ...mapActions(["initCache"]),
    catchError(err) {
      switch (err.code) {
        case 4001:
          this.alert = "Не удалось понять запрос";
          break;
        case 4002:
          this.alert = "Запрос построен неверно";
          break;
        case 4004:
          this.alert = "Неизвестный метод";
          break;
        case 4003:
        case 4005:
          this.alert = err.msg;
          break;
        case 4006:
          this.alert = "Ошибка парсера, попробуйте еще раз";
          break;
        case 4007:
          this.alert = "Для этого метода нужна авторизация в сетевом";
          break;
        case 4008:
          this.alert = "Сетевой город не отвечает";
          break;
        case 4009:
          this.$router.replace("login");
          this.alert = "Для начала нужно войти в систему";
          break;
        default:
          this.alert = "Неизвестная ошибка, попробуйте ещё раз";
      }
      console.error(err);
    }
  },
  computed: {
    ...mapState({
      connectedUsers: state => state.settings.storage.connectedUsers
    }),
    uid: {
      get() {
        return this.$store.state.settings.storage.uid;
      },
      set(val) {
        this.$store.state.settings.storage.uid = val;
        return true;
      }
    },
    name() {
      return this.$route.name;
    }
  },
  mounted() {
    this.$root.$on("catch-error", this.catchError);
    this.$root.$children[0]
      .checkExist()
      .then(exist => {
        if (!exist) throw void 0;
        this.initCache().then(() => (this.loading = false));
      })
      .catch(() => this.$router.replace("login"));
  },
  components: {
    Loader,
    Sidebar,
    UserInfo
  }
};
</script>

<style>
.content {
  margin-bottom: 56px;
}
.content > div#header {
  display: flex;
  align-items: center;
  flex-direction: row;
  padding: 0.4rem 0.8rem 0;
  justify-content: space-between;
}
.content > div#header .md-button {
  height: fit-content;
  text-transform: none;
}
.content > div#header .md-button div.user-info div {
  width: calc(100% - 50px);
}
.content > div#header .md-button div.user-info div span {
  overflow: hidden;
  max-width: 100%;
}
.content ~ div.md-menu-content li.flex-end .md-list-item-content {
  justify-content: flex-end;
}
.md-snackbar {
  z-index: 1001 !important;
}
</style>
