<template>
  <div class="lesson md-layout md-gutter md-alignment-center-left">
    <span class="number md-layout-item md-size-5 md-body-1">{{ number }}</span>
    <div class="info md-layout-item md-size-80">
      <span class="md-body-2">{{ name }}</span>
      <span class="md-caption"
        >{{ startTime }} - {{ endTime }}, {{ room }}
      </span>
      <span
        class="homework md-body-1"
        @click="e => e.target.classList.toggle('show')"
        >{{ homework }}</span
      >
    </div>
    <div class="assignments md-layout-item md-size-10 md-body-2">
      <span
        :key="assignment.id"
        v-for="assignment in assignments"
        :class="{ bad: assignment.mark <= 3 }"
        @click="$root.$emit('show-mark', assignment)"
        >{{ assignment.mark }}</span
      >
    </div>
  </div>
</template>

<script>
export default {
  props: [
    "name",
    "room",
    "number",
    "endTime",
    "homework",
    "startTime",
    "assignments"
  ]
};
</script>
