<template>
  <md-dialog
    :md-active="!!id"
    :md-fullscreen="false"
    @md-opened="this.loadMark"
    @update:mdActive="
      close => {
        if (!close) {
          $emit('close');
          this.date = false;
        }
      }
    "
  >
    <md-dialog-title>Информация о задании</md-dialog-title>

    <Loader v-if="loading" />

    <transition name="info">
      <div v-if="!loading && date" :class="{ stripes }" class="info">
        <div>
          <div class="md-body-2">Дата:</div>
          <div class="md-body-1">{{ date | date }}</div>
        </div>
        <div>
          <div class="md-body-2">Вес:</div>
          <div class="md-body-1">{{ weight }}</div>
        </div>
        <div>
          <div class="md-body-2">Оценка:</div>
          <div class="md-body-1">{{ mark }}</div>
        </div>
        <div>
          <div class="md-body-2">Учитель:</div>
          <div class="md-body-1" @click="showCell">{{ teacher }}</div>
        </div>
        <div>
          <div class="md-body-2">Предмет:</div>
          <div class="md-body-1" @click="showCell">{{ subject }}</div>
        </div>
        <div>
          <div class="md-body-2">Тип задания:</div>
          <div class="md-body-1" @click="showCell">{{ type }}</div>
        </div>
        <div>
          <div class="md-body-2">Тема задания:</div>
          <div class="md-body-1" @click="showCell">{{ theme }}</div>
        </div>
      </div>
    </transition>
  </md-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Loader from "@/components/Loader";

export default {
  props: {
    id: {
      type: Number,
      default: null
    },
    mark: {
      type: Number,
      default: null
    },
    type: {
      type: String,
      default: null
    }
  },
  data: () => ({
    loading: true,
    date: null,
    weight: null,
    teacher: "",
    subject: "",
    theme: ""
  }),
  watch: {
    id() {
      this.loadMark();
    }
  },
  computed: mapState({
    stripes: state => state.settings.storage.diaryStripes
  }),
  methods: {
    ...mapActions(["GetMark"]),
    loadMark() {
      this.loading = true;
      if (!this.id) return;
      this.GetMark({ id: this.id })
        .then(mark => {
          this.date = new Date(mark.date);
          this.theme = mark.name;
          this.weight = mark.weight;
          this.subject = mark.subject;
          this.teacher = mark.teacher;
        })
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.loading = false));
    },
    showCell(e) {
      e.target.classList.toggle("show");
    }
  },
  components: {
    Loader
  }
};
</script>

<style>
.md-dialog div.info {
  padding: 6px 24px 24px;
}
.md-dialog div.info > div {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.md-dialog div.info > div {
  padding-top: 5px;
  padding-bottom: 5px;
  border-top: 1px solid rgba(0, 0, 0, 0.12);
}
.md-dialog div.info > div:first-child,
.md-dialog div.info:not(.stripes) > div {
  border-width: 0;
}
.md-dialog div.info > div > div:first-child {
  margin-right: 8px;
  white-space: nowrap;
}
.md-dialog div.info > div > div:last-child {
  width: 100%;
  cursor: pointer;
  overflow: hidden;
  line-height: 24px;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.md-dialog div.info > div > div.show {
  white-space: normal;
}
.md-dialog .info-enter-active,
.md-dialog .info-leave-active {
  max-width: 100vw;
  max-height: 100vh;
  transition: all 0.6s ease;
}
.md-dialog .info-enter,
.md-dialog .info-leave-to {
  opacity: 0;
  margin: 0 !important;
  padding: 0 !important;
  max-width: 0 !important;
  max-height: 0 !important;
}
</style>
