<template>
  <div class="diary">
    <md-progress-bar
      md-mode="determinate"
      :md-value="countdown.percent"
      v-if="countdown.time_left > 0"
    ></md-progress-bar>
    <Loader v-if="week.loading" />
    <div class="inputs">
      <md-field
        class="md-layout-item md-xsmall-size-100 md-small-size-60 md-medium-size-40 md-large-size-30 md-xlarge-size-20"
      >
        <label>Неделя</label>
        <md-select v-model="week.selected" :disabled="week.loading">
          <md-option
            v-for="(range, index) in week.rangers"
            :key="index"
            :value="index"
          >
            {{ range.start | date }} - {{ range.end | date }}</md-option
          >
        </md-select>
      </md-field>
      <div
        class="md-layout-item md-xsmall-size-100 md-small-size-60 md-medium-size-40 md-large-size-30 md-xlarge-size-20"
      >
        <div class="md-layout md-alignment-center-space-around">
          <md-button
            class="md-primary"
            :disabled="week.loading"
            @click="week.selected--"
          >
            <md-icon>navigate_before</md-icon>
          </md-button>
          <md-button
            class="md-primary"
            :disabled="week.loading"
            @click="week.selected++"
          >
            <md-icon>navigate_next</md-icon>
          </md-button>
        </div>
      </div>
    </div>
    <div class="md-layout md-alignment-top-space-around">
      <Day v-for="day in diary" :key="day.date" :day="day" />
    </div>
    <ViewMark
      :id="viewMark.id"
      :mark="viewMark.mark"
      :type="viewMark.type"
      @close="viewMark.id = null"
    />
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Day from "@/components/DiaryDay";
import Loader from "@/components/Loader";
import ViewMark from "@/components/ViewMark";

export default {
  data: () => ({
    week: {
      rangers: [],
      get selected() {
        return this._selected || 0;
      },
      set selected(val) {
        if (val < 0) val = 0;
        if (val >= this.rangers.length) val = this.rangers.length - 1;
        return (this._selected = val);
      },
      loading: true
    },
    countdown: {
      enable(time) {
        return new Promise(res => {
          this.work = true;
          this.time_start = this.time_left = time;
          this.interval = setInterval(() => {
            this.time_left -= 100;
            if (this.time_left <= 0) {
              this.disable();
              res();
            }
          }, 100);
        });
      },
      disable() {
        this.work = false;
        clearInterval(this.interval);
        this.time_start = this.time_left = 0;
        return this;
      },
      get percent() {
        return (this.time_left * 100) / this.time_start;
      },
      work: false,
      time_left: 0,
      time_start: 0,
      interval: null
    },
    diary: [],
    allTypes: [],
    viewMark: {
      id: null,
      mark: null,
      type: null
    }
  }),
  computed: mapState({
    TimeLeft: state => state.settings.storage.diaryCountdown,
    SundayNewWeek: state => state.settings.storage.noIncludeSunday
  }),
  methods: {
    ...mapActions([
      "GetTypes",
      "GetRange",
      "GetDiary",
      "getCache",
      "saveCache",
      "InitListiner",
      "RemoveListiner"
    ]),
    init() {
      this.diary = [];
      this.allTypes = [];
      this.week.rangers = [];
      this.week.selected = 0;
      this.week.loading = true;
      this.countdown.disable();
      this.GetRange()
        .then(this.setSelected)
        .then(() => this.GetTypes())
        .then(types => (this.allTypes = types))
        .then(() =>
          this.getCache({
            time: 12e5,
            name: "DiarySelected",
            next: () => this.week.selected
          })
        )
        .then(i => {
          this.week.selected = i;
          this.loadDiary(this.week.rangers[i]);
        })
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.week.loading = false));
    },
    setSelected(range) {
      if (!range?.start || !range?.end)
        throw new Error("For initialization, pass the start and end values");
      const start = new Date(range.start);
      const end = new Date(range.end);
      while (start < end) {
        const startWeek = new Date(start);
        do {
          start.setDate(start.getDate() + 1);
        } while (start.getDay() != 0);

        this.week.rangers.push({
          start: startWeek,
          end: new Date(start)
        });
        start.setDate(start.getDate() + 1);
      }
      for (let i = 0; i < this.week.rangers.length; i++) {
        const { start, end } = this.week.rangers[i];
        if (Date.now() >= start && Date.now() <= +end + 864e5) {
          if (new Date().getDay() == 0 && this.SundayNewWeek) i++;
          this.week.selected = i;
          break;
        }
      }
      return true;
    },
    loadDiary(range) {
      this.week.loading = true;
      this.GetDiary(range)
        .then(data => (this.diary = data))
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.week.loading = false));
    },
    showMark({ id, mark, type }) {
      this.viewMark.id = id;
      this.viewMark.mark = mark;
      this.viewMark.type = this.allTypes.find(t => t.id == type)?.name;
    }
  },
  watch: {
    "week.selected"(value) {
      if (!this.diary.length) return;
      this.countdown
        .disable()
        .enable(this.TimeLeft * 1000)
        .then(() => this.loadDiary(this.week.rangers[value]))
        .then(() =>
          this.saveCache({
            name: "DiarySelected",
            value
          })
        );
    }
  },
  created() {
    this.init();
    this.$root.$on("show-mark", this.showMark);
  },
  destroyed() {
    this.countdown.disable();
  },
  components: {
    Day,
    Loader,
    ViewMark
  }
};
</script>

<style>
div.diary div.inputs {
  margin: 0 0.8rem;
}
div.diary .md-progress-bar {
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
}
div.diary .md-field.md-layout-item {
  margin-bottom: 10px;
}
div.diary .md-card {
  margin: 14px;
}
div.diary .md-card > .md-card-header {
  text-align: center;
}
div.diary div.lesson {
  padding-top: 5px;
  padding-bottom: 5px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
}
div.diary div.lesson:last-child,
div.diary div.lesson:not(.stripes) {
  border-width: 0;
}
div.diary div.lesson > span.number {
  text-align: center;
}
div.diary div.lesson > div.info {
  display: flex;
  flex-direction: column;
}
div.diary div.lesson > div.info > span.md-caption {
  margin-top: -2px;
  line-height: 10px;
  margin-bottom: 2px;
}
div.diary div.lesson > div.info > span.homework {
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
div.diary div.lesson > div.info > span.homework.show {
  white-space: normal;
}
div.diary div.lesson > div.assignments {
  display: flex;
  align-self: center;
  flex-direction: row;
  justify-content: space-around;
}
div.diary div.lesson > div.assignments > span {
  cursor: pointer;
  font-size: 16px;
  font-weight: 400;
}
div.diary div.lesson > div.assignments > span.bad {
  color: #ff033e;
  font-weight: 450;
}
</style>
