<template>
  <component :is="layout">
    <router-view />
  </component>
</template>

<script>
import { mapState, mapActions } from "vuex";
import MainLayout from "@/layouts/MainLayout.vue";

export default {
  computed: {
    ...mapState({
      settings: state => state.settings.storage,
      connectedUsers: state => state.settings.storage.connectedUsers
    }),
    uid: {
      get() {
        return this.$store.state.settings.storage.uid;
      },
      set(val) {
        this.$store.state.settings.storage.uid = val;
        return true;
      }
    },
    layout() {
      if (!this.$route.meta?.layout) return "div";
      return this.$route.meta?.layout + "-layout";
    }
  },
  watch: {
    settings: {
      handler() {
        this.saveSettings();
      },
      deep: true
    },
    uid(id) {
      this.ChangeUser(id).then(() =>
        this.$root.$children[0]?.$children[0]?.$children[3]?.init?.()
      );
    }
  },
  methods: {
    ...mapActions(["saveSettings", "ChangeUser"]),
    checkExist() {
      function makeFetch(users = [], i = 0) {
        return i < users.length
          ? fetch(
              `//${process.env.VUE_APP_host || location.hostname}/exist/${
                users[i]
              }`
            )
              .then(res => res.json())
              .then(exist => {
                if (!exist) users.splice(i, 1);
                else i++;
              })
              .finally(() => makeFetch(users, i))
          : new Promise(res => res(void 0));
      }
      return makeFetch(this.connectedUsers).then(() => {
        if (this.connectedUsers.length == 0) return false;
        if (!this.connectedUsers.includes(this.uid)) {
          this.uid = this.connectedUsers[0];
        }
        return true;
      });
    }
  },
  components: {
    MainLayout
  },
  mounted() {
    this.$connect(
      `//${process.env.VUE_APP_host || location.host}/?uid=${this.uid}`
    );
  }
};
</script>
